.header
{
    background-color: white;
    width: 100%;
    position: fixed;
}
.header > .container
{
    display: flex;
    justify-content: space-between;
} 
.header_menu 
{
    display: flex;
}
.header_item
{
    display: flex;
}
.header_item :hover
{
    background-color:rgba(0, 0, 0, 0.171);
}
.header_item > div
{
    padding: 20px 15px ;
    align-self: center;
    transition: 0.2s;
}

.header_logo
{
    width: 140px;
    padding: 0 20px;
    transition: 0.2s;
}

.header_config
{
    display: flex;
    align-items: center;
}

.config_search
{
    margin: 0 10px;
    border-radius: 5px;
    height: 40px;
    padding: 0 10px;
    display: flex;
    align-items: center;
}
.search_text
{
    background: none;
    color: black;
    background: none;
    font-size: 14px;
    line-height: 16px;
    outline: none;
    border: none;
    width: 0;
    transition: 0.4s;
}
.search_btn
{
    width:20px;
}
.config_search:hover > .search_text
{
    width: 100px;
}
.config_search:hover 
{
    border: 1px solid black;
}

.mob_menu{
    display: none;
    margin: 10px;
}

.mob_menulist
{
    display: none;
}


.config_select
{
    border: none;
    margin: 0 5px;
    outline: none;
}
.listbox_lang, .listbox_val
{
    display: none;
}

.search_lang, .search_val
{
    width: 70px;
    border: none;
    outline: none;
}


.config_but button
{
    background: none;
    outline: none;
    border: none;
    padding: 0 30px;
    width:20px;
    display: flex;
    align-items: center;
}



