@import url(https://fonts.googleapis.com/css2?family=Baloo+Da+2&display=swap);
.header
{
    background-color: white;
    width: 100%;
    position: fixed;
}
.header > .container
{
    display: flex;
    justify-content: space-between;
} 
.header_menu 
{
    display: flex;
}
.header_item
{
    display: flex;
}
.header_item :hover
{
    background-color:rgba(0, 0, 0, 0.171);
}
.header_item > div
{
    padding: 20px 15px ;
    align-self: center;
    transition: 0.2s;
}

.header_logo
{
    width: 140px;
    padding: 0 20px;
    transition: 0.2s;
}

.header_config
{
    display: flex;
    align-items: center;
}

.config_search
{
    margin: 0 10px;
    border-radius: 5px;
    height: 40px;
    padding: 0 10px;
    display: flex;
    align-items: center;
}
.search_text
{
    background: none;
    color: black;
    background: none;
    font-size: 14px;
    line-height: 16px;
    outline: none;
    border: none;
    width: 0;
    transition: 0.4s;
}
.search_btn
{
    width:20px;
}
.config_search:hover > .search_text
{
    width: 100px;
}
.config_search:hover 
{
    border: 1px solid black;
}

.mob_menu{
    display: none;
    margin: 10px;
}

.mob_menulist
{
    display: none;
}


.config_select
{
    border: none;
    margin: 0 5px;
    outline: none;
}
.listbox_lang, .listbox_val
{
    display: none;
}

.search_lang, .search_val
{
    width: 70px;
    border: none;
    outline: none;
}


.config_but button
{
    background: none;
    outline: none;
    border: none;
    padding: 0 30px;
    width:20px;
    display: flex;
    align-items: center;
}





.main_table 
{
    margin: 0 auto;
    height: 500px;
    overflow: scroll;
    background-color: white;
    border-collapse: collapse;
    font-size: 26px;
    table-layout: fixed;
    width: 100%;
}

.value_down 
{
    background-color: rgb(255, 198, 198)
}
.value_up
{
    background-color: rgb(185, 239, 255);
}
.left
{
    border-top-left-radius: 10px;
}

.main_table td,th
{   
    padding: 10px 30px;
    font-weight: lighter;
}

.main_table th 
{
    border-top: none;
    font-size: 18px;
    background-color: rgba(0, 0, 0, 0.062);
}

.main_table tbody tr
{
    border-bottom: 1px solid rgba(0, 0, 0, 0.253);
    transition: 0.4s;
}
.main_table tbody tr:hover   
{
    background: rgba(0, 0, 0, 0.144);
}



.blueback
{
    width: 100%;
    height: 300px;
    background: rgb(131,204,198);
    background: linear-gradient(90deg, rgba(131,204,198,1) 0%, rgba(151,255,140,1) 50%, rgba(132,234,255,1) 100%);
}

.scroll
{   
    margin: -150px auto;
    height: 850px;
    width: 100%;
    overflow: scroll;
    overflow-x:hidden; 
    border-radius: 10px;
    scroll-padding-right: -10px;
}

*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    color: black;
}
li
{
    list-style: none;
}
.container
{
    width:  960px;
    margin: 0 auto;
}

body
{
    font-family: 'Baloo Da 2', cursive;
    overflow: hidden;
}

@media screen and (max-width: 960px)
{
    .mob_hidden{
        display: none;
    }
    .container{
        width: 100%;
        min-width: 280px;
    }

    .container > .header_item{
        order: 1;
    }
    .header_menu
    {
        order: 2;
    }
    .header_config
    {
        order: 0;
    }
    .config_select , .config_but
    {
        display: none;
    }
    .mob_menulist{
        font-size: 12px;
        display: block;
    }
    .header_menu > .header_item
    {
        display: none;
    }
    
}

