
.main_table 
{
    margin: 0 auto;
    height: 500px;
    overflow: scroll;
    background-color: white;
    border-collapse: collapse;
    font-size: 26px;
    table-layout: fixed;
    width: 100%;
}

.value_down 
{
    background-color: rgb(255, 198, 198)
}
.value_up
{
    background-color: rgb(185, 239, 255);
}
.left
{
    border-top-left-radius: 10px;
}

.main_table td,th
{   
    padding: 10px 30px;
    font-weight: lighter;
}

.main_table th 
{
    border-top: none;
    font-size: 18px;
    background-color: rgba(0, 0, 0, 0.062);
}

.main_table tbody tr
{
    border-bottom: 1px solid rgba(0, 0, 0, 0.253);
    transition: 0.4s;
}
.main_table tbody tr:hover   
{
    background: rgba(0, 0, 0, 0.144);
}


