*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    color: black;
}
li
{
    list-style: none;
}
.container
{
    width:  960px;
    margin: 0 auto;
}

body
{
    font-family: 'Baloo Da 2', cursive;
    overflow: hidden;
}

@media screen and (max-width: 960px)
{
    .mob_hidden{
        display: none;
    }
    .container{
        width: 100%;
        min-width: 280px;
    }

    .container > .header_item{
        order: 1;
    }
    .header_menu
    {
        order: 2;
    }
    .header_config
    {
        order: 0;
    }
    .config_select , .config_but
    {
        display: none;
    }
    .mob_menulist{
        font-size: 12px;
        display: block;
    }
    .header_menu > .header_item
    {
        display: none;
    }
    
}